import cx from "classnames";
import PageTitle from "../PageTitle";
import Description from "../Description";
import { useMediaQuery } from "@uidotdev/usehooks";
import { Link } from "react-router-dom";
import RoundButton from "../RoundButton";
import ScrollButton from "components/ScrollButton";

// ================================================

const HowToEarn = ({ earnRaijinsTicketCardContents }) => {
  const isMobile = useMediaQuery("only screen and (max-width: 640px)");

  const onBuyBtn = () => {
    const dom = document.querySelector("#buy-tickets");
    if (dom) {
      dom.scrollIntoView({ behavior: "smooth" });
    }
  };
  return (
    <div className={cx("how-to-earn")}>
      <PageTitle classes="earn-loh-tickets-title">
        how to earn <span>loh tickets</span>?
      </PageTitle>
      <Description
        classes={cx(
          "text-center",
          isMobile ? "w-100" : "w-30",
          "mx-auto",
          "mb-3"
        )}
      >
        There are four ways to earn Raijins tickets
      </Description>
      <div className={cx("cards-row")}>
        <div className={cx("earn-loh-tickets-card")}>
          <div className={cx("card-media", "mb-3")}>
            <img
              src={earnRaijinsTicketCardContents[0].image}
              alt={`${earnRaijinsTicketCardContents[0].title}.png`}
              width={80}
            />
          </div>
          <div className={cx("card-content")}>
            <h5 className={cx("title", "mb-2", "uppercase")}>
              {earnRaijinsTicketCardContents[0].title}
            </h5>
            <p className={cx("content")}>{earnRaijinsTicketCardContents[0].desc}</p>
          </div>
          <div className={cx("card-action")}>
            <Link to="/staking" className="round-btn primary">
              {earnRaijinsTicketCardContents[0].actionText}
            </Link>
          </div>
        </div>
        <div className={cx("earn-loh-tickets-card")}>
          <div className={cx("card-media", "mb-3")}>
            <img
              src={earnRaijinsTicketCardContents[1].image}
              alt={`${earnRaijinsTicketCardContents[1].title}.png`}
              width={80}
            />
          </div>
          <div className={cx("card-content")}>
            <h5 className={cx("title", "mb-2", "uppercase")}>
              {earnRaijinsTicketCardContents[1].title}
            </h5>
            <div
              className={cx("content")}
              dangerouslySetInnerHTML={{
                __html: earnRaijinsTicketCardContents[1].desc,
              }}
            />
          </div>
          <div className={cx("card-action")}>
            <RoundButton
              text={earnRaijinsTicketCardContents[1].actionText}
              variant="primary"
              onBtnClick={onBuyBtn}
            />
          </div>
        </div>
        <div className={cx("earn-loh-tickets-card")}>
          <div className={cx("card-media", "mb-3")}>
            <img
              src={earnRaijinsTicketCardContents[2].image}
              alt={`${earnRaijinsTicketCardContents[2].title}.png`}
              width={80}
            />
          </div>
          <div className={cx("card-content")}>
            <h5 className={cx("title", "mb-2", "uppercase")}>
              {earnRaijinsTicketCardContents[2].title}
            </h5>
            <p className={cx("content")}>{earnRaijinsTicketCardContents[2].desc}</p>
          </div>
          <div className={cx("card-action")}>
            <a
              className="round-btn primary"
              href="https://discord.com/invite/life-of-hel"
              target="_blank"
            >
              {earnRaijinsTicketCardContents[2].actionText}
            </a>
          </div>
        </div>
        <div className={cx("earn-loh-tickets-card")}>
          <div className={cx("card-media", "mb-3")}>
            <img
              src={earnRaijinsTicketCardContents[3].image}
              alt={`${earnRaijinsTicketCardContents[3].title}.png`}
              width={80}
            />
          </div>
          <div className={cx("card-content")}>
            <h5 className={cx("title", "mb-2", "uppercase")}>
              {earnRaijinsTicketCardContents[3].title}
            </h5>
            <p className={cx("content")}>{earnRaijinsTicketCardContents[3].desc}</p>
          </div>
          <div className={cx("card-action")}>
            <a className="round-btn primary">
              {earnRaijinsTicketCardContents[3].actionText}
            </a>
          </div>
        </div>
      </div>
      {
        isMobile ?
          null
          :
          <div className="scrollbtn-wrapper">
            <ScrollButton />
          </div>
      }
    </div>
  );
};

export default HowToEarn;
